@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.node{
    width: 25px;
    height: 25px;
    outline: 1px solid rgb(191, 233, 233);
    display: inline-block;
}

.node-finish {
    background-color: rgb(223, 5, 70);
}

.node-start {
    background-color: rgb(4, 255, 100);
}

.node-visited {
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      background-color: rgba(9, 6, 180, 0.75);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgba(17, 97, 217, 0.75);
    }
  
    75% {
      transform: scale(1.2);
      background-color: rgba(0, 217, 159, 0.75);
    }
  
    100% {
      transform: scale(1);
      background-color: rgba(0, 190, 218, 0.75);
    }
  }
  
  .node-wall {
    animation-name: wallAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }

  @keyframes wallAnimation {
    50% {
      transform: scale(0.9);
      background-color: rgba(179, 11, 194, 0.904);
      }
    100% {
      transform: scale(1);
      background-color: rgb(70, 0, 128);
    }
  }
  
  .node-shortest-path {
    animation-name: shortestPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes shortestPath {
    0% {
      transform: scale(0.6);
      background-color: rgb(240, 240, 173);
    }
  
    50% {
      transform: scale(1.2);
      background-color: rgb(255, 254, 106);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(253, 253, 0);
    }
  }
li,a, button{
  font-family: "Montserrat", "sans-serif";
  font-weight: 600;
  font-size: 16px;
  color: #edf0f1;
  text-decoration: none;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  width: 30vw;
  margin-left: 5px;
  cursor: pointer;
}

.fa-react {
  margin-left: 0.2rem;
  font-size: 1.6rem;
}

.header{
  width: 100%;
  height: 100px;
  display: block;
  background-color: #24252A;
  margin-bottom: 10px;
}
.header-bottom{
  width: 100%;
  height: 25px;
  display: block;
  background-color: #24252A;
  margin-top: 10px;

  position: fixed; 
  bottom:0%; 
}

.inner_header{
  width: 1400px;
  height: 100%;
  display:block;
  margin: 0 auto;
}
.inner_header_buttons{
  width: 600px;
  height: 100%;
  display:block;
  float:right;
  margin-left: 10px;
}

.logo{
  cursor: pointer;
  vertical-align: middle;
}

.logo_container{
  height:100%;
  display: table;
  float: left;
  vertical-align: middle;
}

.logo_container h1{
  color:white;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  font-family: 'Montserrat';
  font-size: 32px;
  font-weight: 800;
}
.logo_contsiner h1 span{ 
  font-weight: 200;
}

.navigation{
  float: left;
  height: 100%;
}

.navigation a {
  height: 100%;
  display: table;
  float: right;
  padding: 0px 2px;
}

.navigation a li {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  color: white;
  font-family: 'Montserrat';
  font-size: 16px;
}
.nav-menu {
  display: flex;
  flex-direction: column;
  width:100%;
  height: 500px;
  position: absolute;
  top: 70px;
  left: -100%;
  opacity: 1;
  transition: all 0.5s ease;
}

.nav-menu.active {
  background: #24252A;
  left: 0;
  opacity: 1;
  transition: all 0.5s ease;
  z-index: 1;
}

.nav__links {
  list-style:none;
}
.nav__links li{
  display: inline-block;
  padding: 0px 30px;
}
.nav__links li a {
  transition: all 0.3s ease-out;
}
.nav__links li a:hover{
  color:#0088a9;
}
button{
  padding:9px 25px;
  background-color: rgba(0,136,169,1);
  border: none;
  border-radius: 50px;
  cursor: pointer; 
  transition: all 0.3s ease 0s;
}
button:hover {
  background-color: rgba(0,136,169,0.8);
}

.grid {
  margin: 100px 0 0;
}

